import { useState } from "react";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormInput from "../../../../Ui/Input/FormInput";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useDepartmentAddPso } from "../../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useTranslation } from "react-i18next";
import style from "../../../FinanceManager/Income/Income.module.css";
import FormTextarea from "../../../../Ui/Input/FormTextarea";
const AddDepartmentSitePso = ({ onClose, onRefetch, dsid }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    title: "",
    description: "",
  });
  const [departmentAddPso] = useDepartmentAddPso();

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onAdd = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && dsid) {
      setDisabled((pre) => !pre);
      departmentAddPso({
        dsid: dsid,
        bodyParameter: state,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  //   console.info("fdsfgfsd", dsid, errorField);

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("add_department_po_pso")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("title")}
              placeholder={t("title_placeholder")}
              name={"title"}
              value={state.title}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["title"]}
            />
            <FormTextarea
              label={t("description")}
              placeholder={t("description_placeholder")}
              name="description"
              type="text"
              value={state.description}
              onChange={onChange}
              customFormInputContainer={{
                width: "100%",
              }}
            />
            <CreateButton label="add" onAction={onAdd} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default AddDepartmentSitePso;
