import { useState } from "react";
import { useDepartmentDashboardSite } from "../../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import Loading from "../../../../Loader/Loading";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import AddDepartmentSitePso from "./AddDepartmentSitePso";
import CreateButton from "../../../../Ui/Button/CreateButton";
import style from "../../../FinanceManager/FinanceManager.module.css";
import DepartmentSitePsoCard from "./DepartmentSitePsoCard";
import { useTranslation } from "react-i18next";
const DepartmentSitePso = ({ did, dsid }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const {
    departmentDashboardSite,
    departmentDashboardSiteLoading,
    departmentDashboardSiteRefetch,
  } = useDepartmentDashboardSite({
    data: {
      did: did,
    },
    skip: !did,
  });

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("department_pso")}</h6>
            <CreateButton
              label="add"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onToggle}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {departmentDashboardSite?.po_pso?.map((pso) => (
            <DepartmentSitePsoCard pso={pso} key={pso?._id} />
          ))}
          {departmentDashboardSiteLoading ? (
            <Loading />
          ) : (
            departmentDashboardSite?.po_pso?.length === 0 && (
              <EmptyMessage title="No department pso." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>

      {add && (
        <AddDepartmentSitePso
          onClose={onToggle}
          dsid={dsid}
          onRefetch={departmentDashboardSiteRefetch}
        />
      )}
    </>
  );
};

export default DepartmentSitePso;
